@import "../../theme/styles/var";

.container {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: $darkGreen;
}

.innerContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  color: white;
}

.verticalLine {
  border-left: 1px solid white;
  height: 20px;
  margin-left: 25px;
  margin-right: 25px;
}

.flexRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.contentAfterIcon {
  padding-left: 5px;
}

.centerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
